.sidebar-collapse {
  visibility: hidden;
  position: fixed;
  top: 0;
  width: 100vh;
}

.sidebar-collapse.show {
  visibility: visible;
  border-left: medium solid rgb(255, 140, 0);
  z-index: 1035;
}

.sidebar-collapse-item:hover {
  color: rgb(255, 140, 0) !important;
}

.nav-list-item {
  color: #888;
}

.nav-list-item:hover {
  box-shadow: inset 3px 0 0 0 rgb(255, 140, 0) !important;
  /* border-left: thick solid rgb(255,140,0) !important; */
  border-bottom: thin solid rgb(255, 140, 0) !important;
  border-left: thin solid rgb(255, 140, 0) !important;
  background: transparent !important;
}

.nav-list-item:active,
.nav-list-item:focus {
  box-shadow: none;
}

.nav-list-item:active {
  background-color: transparent !important;
  box-shadow: none !important;
  color: gray !important;
}

.nav-list-item:active svg {
  background-color: transparent !important;
  box-shadow: none !important;
  color: rgb(255, 140, 0) !important;
}

.sidebar-nav-dropdown-button {
  height: 18px;
}

.vertical-text {
  writing-mode: vertical-rl;
  text-orientation: upright;
  font-family: monospace;
}

.rotated-text {
  writing-mode: vertical-rl;
  font-family: monospace;
  letter-spacing: 2px;
}

.outline-none:focus {
  outline: none;
}

/* ---------------------------------------------------
    SIDEBAR STYLE
----------------------------------------------------- */

.wrapper {
  display: flex;
}

.sidebar {
  z-index: 1000 !important;
}

.sidebar-dropdown-item {
  font-size: 1rem;
}

.sidebar-menu-link {
  font-size: 0.8rem;
  color: #888;
  text-align: left;
}

.sidebar-menu-link:hover {
  border-right: thin solid rgb(255, 140, 0);
  color: #888;
}

.sidebar-menu-heading-link {
  border-right: medium solid transparent;
  color: #888;
}

.sidebar-menu-list-group .sidebar-menu-heading-link {
  border-right: medium solid transparent;
  color: #888;
  text-align: left;
}

.sidebar-menu-list-group:hover .sidebar-menu-heading-link {
  border-right: medium solid rgb(255, 140, 0) !important;
  /* color: #888; */
}

.nav-item-text {
  font-family: monospace;
  letter-spacing: 0.15rem;
}

.nav-item-icon {
  font-size: 1.5em;
}

.nav-item-collapse-icon {
  font-size: 1.25em;
}

.content-container {
  height: 90vh;
  overflow-y: auto;
  /* background-color: rgba(17, 17, 17, 0.5) !important; */
  background-color: rgba(24, 24, 24, 1) !important;
}

.battle-info-contestant img {
  width: 100%;
  max-width: 100%;
  height: 100px !important;
  max-height: 100px !important;
}

.battle-info-status {
  line-height: 15px;
}

.battle-list ul {
  overflow-y: auto;
  overflow-x: hidden;
  height: 400px;
  max-height: 400px;
}

.featured-battles-controls span {
  width: 32px;
  height: 48px;
}

.toast-container {
  top: 20px !important;
  left: 80px !important;
  max-width: 350px !important;
  z-index: 1036 !important;
}

.notification-button:hover svg {
  color: #222 !important;
}

.notification-menu {
  transform: none !important;
  top: 220px !important;
  bottom: 10px !important;
  left: 0 !important;
  right: 0 !important;
  max-width: 100% !important;
  width: 100% !important;
}

.notification-item {
  border-bottom: thin solid #111 !important;
}

.notification-item:hover {
  background-color: #222 !important;
  border-bottom: thin solid rgb(255,140,0) !important;
}

.text-shadow-none {
  text-shadow: none !important;
}

.relationship-image {
  width: 24px;
  height: 24px;
}

.relationship-list-item:hover {
  background-color: #111 !important;
}

.user-popover-image {
  height: 64px;
}

.favorite-rapper-image {
  width: 48px;
  height: 48px;
}

.favorite-rapper-owner:hover .favorite-rapper-image {
  box-shadow: 2px 3px 10px 0 #444 !important;
}

.favorite-rapper-owner:hover .favorite-rapper-overlay {
  display: block !important;
}

.community-panel {
  width: 30%;
}

.more-profile-options .dropdown-item:hover {
  background-color: rgba(34, 34, 34, 0.5);
}

.people-tab-item:hover a {
  border-bottom-color: rgb(255, 140, 0) !important;
}

.mh-400-px {
  min-height: 400px;
}

.profile-battle-list {
  bottom: 0;
  top: 12%;
}

.edit-group-icon {
  display: none;
}

.edit-button:hover .edit-group-icon,
.edit-group-icon.editing {
  display: inline-block;
}

/* ---------------------------------------------------
    MEDIAQUERIES
----------------------------------------------------- */

@media (max-width: 767px) {
  .page-content {
    padding-top: 60px !important;
    padding-left: 0px !important;
  }

  .sidebar {
    position: fixed !important;
    width: 100vw !important;
    height: 60px !important;
    top: 0;
  }

  .sidebar-collapse {
    top: 60px;
    width: 100vw;
  }

  .toast-container {
    top: 110px !important;
    left: 0px !important;
  }

  .notification-menu {
    top: 35px !important;
    left: 40% !important;
    width: 55% !important;
  }
}

@media (max-width: 575px) {
  .page-content {
    padding-top: 45px !important;
  }

  .sidebar {
    height: 45px !important;
  }

  .sidebar-collapse {
    top: 45px;
    bottom: 0;
  }

  .goto-battle-button {
    width: 50%;
  }

  .battle-info-pane {
    min-height: 128px;
  }

  .battle-info-contestant img {
    width: 32px;
    max-width: 32px;
    height: 32px;
    max-height: 32px;
  }

  .battle-info-contestants-actions {
    flex: 1;
  }

  .sort-battle {
    z-index: 1021 !important;
  }

  .toast-container {
    top: 85px !important;
    left: 0px !important;
  }

  .notification-menu {
    top: 220px !important;
    left: 0 !important;
    right: 0 !important;
    max-width: 100% !important;
    width: 100% !important;
  }

  .community-panel {
    width: 100%;
  }

  .favorite-rapper-image {
    width: 32px;
    height: 32px;
  }
}

@media only screen and (min-width: 768px) {
  .page-content {
    padding-left: 75px !important;
  }

  .sidebar {
    position: fixed !important;
    width: 75px !important;
    top: 0;
    bottom: 0;
    left: 0;
  }

  .nav-item-text {
    writing-mode: vertical-rl;
    text-orientation: upright;
  }

  .nav-item-icon {
    font-size: 2em !important;
  }

  .nav-item-collapse-icon {
    font-size: 2.25em !important;
  }

  .sidebar-collapse {
    left: 75px;
    bottom: 0;
  }

  .sidebar-collapse.show {
    width: 250px;
  }

  .sidebar-menu-link {
    text-align: right;
  }

  .sidebar-menu-list-group .sidebar-menu-heading-link {
    text-align: right;
  }
}

@media only screen and (min-width: 576px) {
  .nav-item-icon {
    font-size: 2em;
  }

  .nav-item-collapse-icon {
    font-size: 1.75em;
  }

  .find-battle-pane {
    position: absolute;
    left: 0;
    right: 0;
    top: 40%;
    bottom: 0;
  }

  .battle-info-pane {
    width: 33%;
  }

  .battle-info-contestant {
    width: 50%;
  }

  .audience-members {
    width: 25%;
    min-height: 250px;
  }

  .favorite-rapper:first-child{
    width: 100%;
  }

  .favorite-rapper:not(:first-child) {
    width: 50%;
}
}
