.sidebar-rating,
.sidebar-rating span svg {
  transform: rotate(180deg);
}

.sidebar-rating {
  max-width: 1rem;
  font-size: 1rem;
}

.popularity-progress {
  height: 0.5rem !important;
}

.popularity-progress.red .progress-bar {
  background-color: red;
}

.popularity-progress.orangered .progress-bar {
  background-color: orangered;
}

.popularity-progress.chartreuse .progress-bar {
  background-color: chartreuse;
}

.popularity-progress.green .progress-bar {
  background-color: green;
}

.popularity-progress.darkgoldenrod .progress-bar {
  background-color: darkgoldenrod;
}

.red {
  color: red;
}

.orangered {
  color: orangered;
}

.chartreuse {
  color: chartreuse;
}

.green {
  color: green;
}

.darkgoldenrod {
  color: darkgoldenrod;
}
