.reacket-player {
  background-color: #222;
  border-left: none;
}

.reacket-player.reacket-highlighted {
  /* background-color: rgb(123, 192, 253); */
  background-color: #333;
  border-left: none;
}

.reacket-player:first-child {
  border-bottom: solid 1px #444;
}

.reacket-winner {
  background-color: #222;
}

.reacket-winner .reacket-player-name,
.reacket-winner .reacket-player-score {
  font-weight: bold;
  color: #28a745;
}

.reacket-highlighted.reacket-winner .reacket-player-score {
  color: white;
}

.reacket-horizontal-line {
  border: solid 1px #111;
}

.reacket-vertical-line {
  border-right: solid 1px #111;
}

.reacket-connector {
  /* width: 1rem; */
}

@media (max-width: 575px) {
  .reacket-match .reacket-players {
    width: 10rem;
  }
}