.battle-info-section {
  border: none;
  box-shadow: 0 0 40px 0 rgba(255, 140, 0, 0.25);
}

.battle-info-section .battle-rating {
  color: green;
}

.judge-panel-item {
  letter-spacing: 2px;
  border-left: thick solid transparent !important;
}

.judge-panel-item:hover {
  border-left: thick solid rgb(255,140,0) !important;
}

.judge-panel-item:active,
.judge-panel-item:focus {
  /* style={{ outline: 'none', letterSpacing: '2px' }} */
  outline: none;
}
