ol.carousel-indicators {
  height: 10px;
}

ol.carousel-indicators li {
  background:  rgb(255,140,0) !important;
}

ol.carousel-indicators li {
  border-top: none;
  height: 0;
  border-radius: 50rem;
}

.round-wrapper, iframe * {
  perspective: 115em;
}

.inner, iframe * {
  transition: .3s;
  transform: rotateY(45deg);
}

.inner-other, iframe * {
  transition: .3s;
  transform: rotateY(-45deg);
}

/* ---------------------------------------------------
    MEDIAQUERIES
----------------------------------------------------- */

@media (max-width: 575px) {
  .inner,
  .inner-other, iframe {
    transform: rotateY(0);
  }

  .carousel-indicators {
    top: 52% !important;
  }
}
