.slider label {
  position: relative;
  top: 0.5rem;
  letter-spacing: 0.4rem;
  font-size: 0.7rem;
  display: block;
}

.slider input {
  appearance: none;
  outline: none;
  width: 100%;
  /* max-width: 200px; */
  height: 0.25rem;
}

.slider input:hover {
  background-color: #444 !important;
}


.slider input::-webkit-slider-thumb {
  appearance: none;
  outline: none;
  width: 1.25rem;
  height: 1.25rem;
  border: 0;
  background: url('/assets/images/adjust-solid.svg');
  cursor: pointer;
}

.slider input::-moz-range-thumb {
  width: 1.25rem;
  height: 1.25rem;
  border: 0;
  background: url('/assets/images/adjust-solid.svg');
  cursor: pointer;
}

.slider datalist {
  font-size: 0.75rem;
}

.slider-number input {
  appearance: none;
  outline: none;
  font-size: 1.5rem;
  width: 2em;
  border: none;
}

.slider-number input::-webkit-inner-spin-button {
  appearance: none;
  width: 1.5rem;
  height: 1.5rem;
  padding: 4px 0;
  background: url('/assets/images/arrows-alt-v-solid.svg') no-repeat center center;
  background-size: contain;
  opacity: 1;
  cursor: pointer;
}

