.verse-card,
.review-card {
  max-height: 265px;
}

.verse-card .card-body,
.verse-menu,
.review-card .card-body {
  height: 32vh;
}

/* .verse-card {
  height: auto;
} */

/* .review-card {
  height: 250px;
} */

.review-card img,
.verse-card img {
  /* height: 181px; */
}

.review-card iframe,
.verse-card iframe {
  height: 175px;
}

.review-card.left,
.verse-card.left {
  box-shadow: 10px 5px 15px 0 rgba(0, 0, 0, 0.75);
}

.review-card.right,
.verse-card.right {
  box-shadow: -10px 5px 15px 0 rgba(0, 0, 0, 0.75);
}

.verse-menu.right {
  left: 20px !important;
}

.verse-menu .dropdown-header {
  border-bottom: thin solid rgb(255, 140, 0) !important;
}

.verse-menu .dropdown-item
.comment-menu .dropdown-header {
  border-right: thin solid transparent;
}

.verse-menu .dropdown-item:hover,
.verse-menu .dropdown-item:active,
.comment-menu .dropdown-item:hover,
.comment-menu .dropdown-item:active {
  outline: none;
  background-color: transparent;
  color: #494f54!important;
}

.verse-menu.right .dropdown-item {
  border-right: thin solid transparent;
}

.verse-menu.right .dropdown-item:hover,
.verse-menu.right .dropdown-item:active {
  border-right: thin solid rgb(255, 140, 0);
}

.verse-menu.left .dropdown-item {
  border-left: thin solid transparent;
}

.verse-menu.left .dropdown-item:hover,
.verse-menu.left .dropdown-item:active {
  border-left: thin solid rgb(255, 140, 0);
}

.move-up {
  top: -23.75vh !important;
}

.reaction-button svg,
.comment-reaction-button svg {
  pointer-events: none;
}

.review-card .card-body .contestant-score,
.verse-card .card-body .contestant-score {
  font-size: 72pt;
}

/* ---------------------------------------------------
    MEDIAQUERIES
----------------------------------------------------- */

@media (max-width: 960px) {
  .verse-menu.right {
    left: 15px !important;
  }
}

@media (max-width: 767px) {
}

@media (max-width: 575px) {
  .verse-menu.right {
    left: 0 !important;
  }
}

@media (max-width: 450px) {

}
