.event-link:hover{
  text-decoration: none;
}

.scroll-box::-webkit-scrollbar {
  width: 0.5em;
}

.scroll-box::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
}

.scroll-box::-webkit-scrollbar-thumb {
  background-color: rgba(0, 0, 0, 0.2);
  outline: 1px solid #222;
  border-left: thin solid rgb(255, 140, 0);;
}

section:last-child {
  height: 40%;
}

.profile-content .video-list {
  width: 30%;
}

.profile-content .video-list.edit .video-list-group {
  box-shadow: inset 0 0 70px 0 rgba(255, 0, 0, 0.5);
}

.profile-content .video-list.edit .mark-video-remove {
  border-right: medium solid red;
  border-left: medium solid red !important;
}

.profile-events {
  overflow-y: scroll;
}

.profile-video-item {
  border-left: medium solid transparent !important;
  border-bottom: thin solid transparent !important;
}

.profile-play-button svg {
  color: #222;
}

.profile-video-item:hover {
  border-left: medium solid rgb(255, 140, 0) !important;
  border-bottom: thin solid rgb(255, 140, 0) !important;
  cursor: pointer;
}

.profile-video-item:hover .profile-play-button svg {
  color: rgb(255,140,0) !important;
}

.youtube-container {
  height: 240px;
}

.profile-photo-container,
.profile-photo-container * {
  height: 240px !important;
}

@media (max-width: 767px) {
  .profile-content .video-list {
    width: 100% !important;
    height: 30vh;
  }
}

@media (max-width: 575px) {
  .profile-info-list {
    width: 100% !important;
  }
}

.pointer-events {
  pointer-events: initial;
}

.no-pointer-events {
  pointer-events: none;
}

.edit-group input.no-style,
.edit-group textarea.no-style {
  background-color: transparent !important;
}

.profile-rating,
.profile-rating span svg {
  transform: rotate(180deg);
}

.profile-rating {
  max-width: 3rem;

}

.profile-rating span svg {
  margin: 0.1rem auto;

}

.overlay {
  transition: 500ms ease;
  opacity: 0;
  z-index: 1000;
}

.social-link a {
  text-decoration: none;
  color: #888;
}

.social-link:hover a {
  color: #666;
}

.overlay {
  opacity: 1;
  transition-delay: 500ms;
}

.overlay button {
  line-height: 1;
}
.remove-button:not(.disabled):hover,
.profile-site-group:hover .remove-button:not(.disabled) {
  color: red !important;
}
